.ni-numeric-input {
  position: relative;
}

.ni-numeric-input.percent input {
  padding-right: 25px !important;
}

.ni-numeric-input.money input {
  padding-left: 25px !important;
}

.ni-numeric-input.percent::after {
  content: attr(data-percent);
  font-size: 1rem;
  display: block;
  font-weight: bold;
  position: absolute;
  top: 6px;
  right: 7px;
}

.ni-numeric-input.money::before {
  content: attr(data-money);
  font-size: 1rem;
  display: block;
  font-weight: 500;
  position: absolute;
  top: 6px;
  left: 4px;
}

.ni-text-right {
  text-align: right;
}
